<h1 id="spring-boot-开发入门" tabindex="-1"><a class="header-anchor" href="#spring-boot-开发入门" aria-hidden="true">#</a> Spring Boot 开发入门</h1>
<h2 id="spring-boot" tabindex="-1"><a class="header-anchor" href="#spring-boot" aria-hidden="true">#</a> Spring Boot</h2>
<h3 id="spring-boot-是什么" tabindex="-1"><a class="header-anchor" href="#spring-boot-是什么" aria-hidden="true">#</a> Spring Boot 是什么</h3>
<h3 id="spring-boot-优点" tabindex="-1"><a class="header-anchor" href="#spring-boot-优点" aria-hidden="true">#</a> Spring Boot 优点</h3>
<ul>
<li>快速构建项目</li>
<li>内嵌服务器（Tomcat,Jetty,Undertow），无需部署 <code>war</code> 包</li>
<li>依赖启动器简化构建配置</li>
<li>自动化配置</li>
<li>整合大量第三方</li>
</ul>
